#contact {
  header {
    height: 233px;
  }

  .contactContainer {
    z-index: 8999;
    position: relative;

    .namePage {
      color: #000;
      font-size: 24px;
      text-transform: uppercase;
      letter-spacing: 1.7px;
    }
    .separator {
      background-color: #b69e62;
      display: block;
      height: 2px;
      width: 136px;
      margin-left: -1px;
      margin-top: 22px;
      margin-bottom: 164px;
    }
    #map {
      width: 100%;
      height: 570px;
      margin-bottom: 169px;
      filter: url("data:image/svg+xml;utf8,<svg xmlns=\'http://www.w3.org/2000/svg\'><filter id=\'grayscale\'><feColorMatrix type=\'matrix\' values=\'0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0 0 0 1 0\'/></filter></svg>#grayscale"); /* Firefox 3.5+ */
      -webkit-filter: grayscale(100%); /* Chrome 19+ & Safari 6+ */
    }
    .aboutMap {
      background-color: #b69e62;
      width: 500px;
      position: absolute;
      top: 222px;
      left: 0;
      .aboutMap__info {
        margin: 58px 0 110px 69px;
        padding: 0;
        list-style-type: none;
        li {
          margin-bottom: 22px;
          span {
            display: inline-block;
            line-height: 22px;
            letter-spacing: 1.7px;
            width: 131px;
            color: #fff;
            opacity: 0.5;
            vertical-align: top;
            &:nth-child(2) {
              width: 231px;
              opacity: 1;
            }
          }
          &:nth-child(2) {
              margin-bottom: 18px;
          }
        }
      }
      .aboutMap__socialnetwork {
        list-style-type: none;
        padding: 0;
        margin: 0 0 70px 199px;
        li {
          display: inline-block;
          margin: 0 46px 0 0;
          &:nth-child(2) {
            margin: 0 49px 0 0;
          }
        }
      }
    }
  }
}
