#page {
  header {
    height: 233px;
  }
  .sectionBlock {
      min-width: 1140px !important;
  }

  .aboutContainer {
    z-index: 8999;
    position: relative;
    margin-bottom: 63px;
    .namePage {
      color: #000;
      font-size: 24px;
      text-transform: uppercase;
      letter-spacing: 1.7px;
    }
    .separator {
      background-color: #b69e62;
      display: block;
      height: 2px;
      width: 136px;
      margin-left: -1px;
      margin-top: 22px;
      margin-bottom: 164px;
    }
    .about__leftBlock {
      position: relative;
      background-color: #fafafa;
      padding: 49px 49px 46px 49px;
      margin: 16px 0 0 21px;
      .about__leftBlock_citate {
        margin: 0;
        padding: 0;
        font-size: 21px;
        text-transform: uppercase;
        font-family: $fontMedium;
        letter-spacing: 1.3px;
        line-height: 24.5px;
        color: #c2ac7c;
      }
      .about__leftBlock_author {
        font-family: $fontMedium;
        font-size: 16px;
        font-style: italic;
        position: absolute;
        bottom: -36px;
        right: 66px;
        text-align: right;
        line-height: 18px;
        color: #c2ac7c;
        span {
          display: block;
          font-family: $font;
        }
      }
    }
    #quotes {
      display: inline-block;
      font-size: 100px;
      font-family: $fontMedium;
      color: #c2ac7c;
      line-height: 69px;
      position: absolute;
      top: -16px;
      left: -28px;
    }
    .about__rightBlock {
      margin: -6px 0 0 97px;
      .about__rightBlock_citate {
        line-height: 24px;
        letter-spacing: 0.4px;
        text-align: justify;
        margin-bottom: 24px;
      }
    }
  }
  .teamContainer {
    position: relative;
    background-image: url(../img/TEAM.png);
    background-repeat: no-repeat;
    background-size: contain;
    margin-bottom: 141px;
    .peopleCommand {
      margin: 83px 0 0 51px;
        p {
          font-size: 16px;
          margin: 14px 0 0 0;
          line-height: 20px;
          font-family: $font;
          width: 232px;
          span {
            display: block;
            font-family: $fontLight;
          }
        }
      }
    }
  .infoContainer {
    margin-bottom: 152px;
    .separator {
      background-color: #ccc;
      height: 2px;
      display: block;
      margin-bottom: 133px;
    }

    .infoContainer__item {
      font-size: 81px;
      font-family: $fontMedium;
      text-align: center;
      color: #c2ac7c;
      span {
        font-size: 15px;
        margin-top: 17px;
        display: block;
        text-transform: uppercase;
        color: #010101;
      }
    }
  }
}
